body {
  background-color: lightseagreen;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
.deviceBody {
  background-color: white;
  margin-top: 15rem;
}
.synthMain {
  justify-content: center;
  display: grid;
}
.synthBorder {
  border-color: 15px solid black;
  width: 50rem;
  height: auto;
}
h1 {
  display: flex;
  justify-content: center;
  font-family: "Libre Barcode 39 Extended Text", cursive;
  font-size: 3em;
}
button {
  margin-top: 8px;
  width: 100px;
  height: 20px;
  text-align: center;
  appearance: none;
  background-color: white;
  border: 1px solid black;
  float: left;
  margin-left: 2em;
}
.waveTypes {
  display: flex;
  justify-content: space-evenly;
  appearance: none;
}

.ctrls {
  display: flex;
  flex-direction: row;
  margin-top: 2em;
  justify-content: space-evenly;
}
/*Synth-Sequencer*/
.sequencerMain {
  justify-content: center;
  display: grid;
  margin-top: 1rem;
}
.subSequencer > input {
  appearance: auto;
  background-color: rgba(0, 0, 0, 0.1);
  margin: 4px 0px;
  height: 50px;
  width: 50px;
  border-radius: 6px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  box-sizing: border-box;
}
.item-selected {
  background-color: rgba(0, 0, 0, 0.8);
}
/* Sliders */
input[type="range"] {
  height: 50px;
  appearance: none;
  margin: 10px 0;
  width: 100%;
}

input[type="range"]:focus {
  outline: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  width: 100%;
  height: 10px;
  cursor: pointer;
  box-shadow: 0px 0px 0px #000000;
  background: #ededed;
  border-radius: 5px;
  border: 1px solid #c2c2c2;
}

input[type="range"]::-webkit-slider-thumb {
  box-shadow: 0px 0px 0px #707070;
  border: 1px solid #a3a3a3;
  height: 42px;
  width: 15px;
  border-radius: 17px;
  background: #ffffff;
  cursor: pointer;
  appearance: none;
  margin-top: -17px;
}
